<template>
  <modal :show.sync="show" @close="close">
    <template slot="header">
      <h5 class="modal-title">{{ title }}</h5>
    </template>
    <div class="tk-popup-content">
      <!-- <template v-if="isHTML"></template> -->
      <!-- <template v-else> -->
      {{ content }}
      <!-- </template> -->
    </div>
    <template slot="footer">
      <base-button
        v-if="left !== ''"
        :type="leftType"
        @click.prevent="onLeftTapped"
        class="tk-font-color-lightGray tk-border-none"
        >{{ left }}</base-button
      >
      <base-button
        v-if="center !== ''"
        :type="centerType"
        @click.prevent="onCenterTapped"
        class="tk-font-color-white tk-bg-color-red tk-border-none"
        >{{ center }}</base-button
      >
      <base-button
        v-if="right !== ''"
        :type="rightType"
        @click.prevent="onRightTapped"
        class="tk-font-color-white tk-bg-color-red tk-border-none"
        >{{ right }}</base-button
      >
    </template>
  </modal>
</template>

<script>
export default {
  props: {
    title: String,
    content: String,
    // isHTML: {
    //   type: Boolean,
    //   default: false,
    // },
    left: {
      type: String,
      default: "",
    },
    center: {
      type: String,
      default: "",
    },
    right: {
      type: String,
      default: "",
    },
    leftType: {
      type: String,
      default: "secondary",
    },
    centerType: {
      type: String,
      default: "secondary",
    },
    rightType: {
      type: String,
      default: "primary",
    },
  },
  data() {
    return {
      show: false,
    };
  },
  mounted() {
    let self = this;
    self.init();
  },
  methods: {
    close() {
      this.$emit("close");
      this.modalClose();
    },
    onLeftTapped() {
      this.$emit("onLeftTapped");
      this.modalClose();
    },
    onCenterTapped() {
      this.$emit("onCenterTapped");
      this.modalClose();
    },
    onRightTapped() {
      this.$emit("onRightTapped");
      this.modalClose();
    },
    modalClose() {
      document.getElementsByTagName("body")[0].classList.remove("modal-open");
    },
    init() {
      let self = this;
      setTimeout(() => {
        self.show = true;
      }, 200);
    },
  },
};
</script>